<template>
    <!-- saleteam-area -->
    <div class="saleteam-area">
        <!-- teams --> 
        <div class="teams" v-for="member in members"  :key="member.id">
            <div class="team-img">
                <img class="img-fluid" src="../../assets/img/Ellipse.svg" alt="">
            </div>

            <div class="team-title">
                <h3>{{member.name}}</h3>
                <p>{{member.tm_name}}</p>
                <p>{{member.role_title}}</p>
            </div>

            <div class="team-tags">
                <p>Permission</p>
                <ul>
                    <li>{{member.permissions}}</li>
                    <comaSplit v-for="(tag, i) in member.permissions.split(',')" :key="i" :tag="tag"></comaSplit>
                    <!-- <li><a href="#">tag</a></li>
                    <li><a href="#">tag</a></li>
                    <li><a href="#">tag</a></li>
                    <li><a href="#">tag</a></li> -->
                </ul>
            </div>
        </div>
        <!-- teams -->
        
        <!-- teams -->
        <div class="teams">
            <div class="add-new">
                <a href="#" data-toggle="modal" data-target="#addMemberModal"><img class="img-fluid" src="../../assets/img/240.svg" alt=""></a>
            </div>
        </div>
        <!-- teams -->

    </div>
    <!-- saleteam-area -->
</template>

<script>
import axios from 'axios'
import comaSplit from '../helpers/comaSplit'
export default {
    name: 'MembersGrid',
    data(){
		return{
            members: null,
            team_id: null
		}
    },
    components:{
        comaSplit
    },
	created(){
		this.getTeamMembers()
    },
    watch: {
        '$route.params.slug': function () {
            this.getTeamMembers()
        }
    },
	methods:{
		getTeamMembers(){
			axios
			.get('teamMembers/'+this.$route.params.slug)
			.then( res => {
				const data = res.data
                this.members = data
                console.log(data);
                this.$root.$on('newMember', function(newMember_data) {
                    //console.log(newMember_data);
                    data.push(newMember_data)
                })
				console.log(res)
			})
			.catch(err =>{
				console.log(err)
			})
		}
    }
}
</script>